export default {
	data() {
		return {
			// 滑块开关
			switchChecked: true,
			// 省市区
			city: ''
		}
	},
	methods: {
		// 切换switch
		changeSwitch: function() {
			this.switchChecked = !this.switchChecked
		},
		// 选择城市
		getCity: function(e) {
			console.log(e.detail.value)
			this.city = e.detail.value[0] + '-' + e.detail.value[1] + '-' + e.detail.value[2]
		}
	}
}
